.details-page {
  padding: 3rem 1rem;
  display: flex;
  justify-content: center;
}

.paper-container {
  padding: 1rem;
  width: fit-content;
}

.content {
  padding: 1rem 0rem;
}

.item-image {
  width: 700px;
  height: 700px;
  background-color: black;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.item-loader {
  padding: 1rem;
}


@media (max-width: 425px){
  
  .item-image{
    width: 100%;
  }
}


